import { NextResponse } from 'next/server';

export async function POST() {
  try {
    // Simula el revalidate de la raíz `/` manualmente
    const revalidated = await fetch('http://localhost:3000/api/revalidate', {
      next: { revalidate: true },
    });

    if (revalidated.ok) {
      return NextResponse.json({ revalidated: true });
    } else {
      throw new Error('Fallo en la revalidación');
    }
  } catch (err) {
    console.error('Error al revalidar:', err);
    return NextResponse.json(
      { error: 'Error al revalidar la página' },
      { status: 500 },
    );
  }
}
